import Typography from "typography"
// import Wordpress2016 from "typography-theme-wordpress-2016"
import fairyGatesTheme from "typography-theme-fairy-gates"

// Wordpress2016.overrideThemeStyles = () => {
//   return {
//     "a.gatsby-resp-image-link": {
//       boxShadow: `none`,
//     },
//   }
// }

// delete Wordpress2016.googleFonts

const chineseFonts = [
  "PingFang SC",
  "Hiragino Sans GB",
  "Droid Sans Fallback",
  "Microsoft YaHei",
]
fairyGatesTheme.headerFontFamily.push(...chineseFonts)
fairyGatesTheme.bodyFontFamily.push(...chineseFonts)
fairyGatesTheme.baseFontSize = "18px"
// fairyGatesTheme.baseLineHeight = 1.75

delete fairyGatesTheme.googleFonts

const typography = new Typography(fairyGatesTheme)

// Hot reload typography in development.
if (process.env.NODE_ENV !== `production`) {
  typography.injectStyles()
}

export default typography
export const rhythm = typography.rhythm
export const scale = typography.scale
